import Link from "next/link";
import { notFound } from "next/navigation";
import {
  getRelatedTreatments,
  getTreatmentById,
} from "../../../src/lib/data/treatments";

export async function generateMetadata({ params }) {
  const { id } = await params;
  const treatment = id ? await getTreatmentById(id) : null;
  if (!treatment) return {};
  return {
    title: treatment.title,
    description:
      treatment.desc ||
      treatment.fullDescription ||
      "Explore this HEALO treatment in Korea.",
  };
}

export default async function TreatmentDetailPage({ params }) {
  const { id } = await params;
  const treatment = id ? await getTreatmentById(id) : null;
  if (!treatment) notFound();

  const related = await getRelatedTreatments(
    treatment.hospitalId,
    treatment.id
  );

  return (
    <main className="max-w-4xl mx-auto px-4 py-10 space-y-8">
      <section className="space-y-3">
        <Link href="/treatments" className="text-sm text-teal-600">
          Back to treatments
        </Link>
        <h1 className="text-3xl font-bold text-gray-900">
          {treatment.title}
        </h1>
        <p className="text-gray-600">
          {treatment.desc || treatment.fullDescription}
        </p>
        <div className="text-sm text-gray-700">
          {treatment.hospitalName} â€¢ {treatment.hospitalLocation}
        </div>
        <div className="text-sm text-gray-700">From {treatment.price}</div>
      </section>

      {related.length > 0 && (
        <section className="space-y-4">
          <h2 className="text-xl font-semibold">Other treatments</h2>
          <div className="grid gap-4 md:grid-cols-2">
            {related.map((item) => (
              <Link
                key={item.id}
                href={`/treatments/${item.id}`}
                className="bg-white rounded-2xl border border-gray-100 p-4 hover:shadow-md transition-shadow"
              >
                <h3 className="font-semibold text-gray-900">{item.title}</h3>
                <p className="text-sm text-gray-500 mt-1 line-clamp-2">
                  {item.desc || item.fullDescription}
                </p>
              </Link>
            ))}
          </div>
        </section>
      )}
    </main>
  );
}
